import {Component, OnInit} from "@angular/core";
import {ClrDatagridNumericFilterInterface, ClrDatagridStringFilterInterface} from "@clr/angular";
import {UnavailableViewSpec} from "@component/unavailable-view/unavailable-view-spec";
import {AlertActionLink} from "@component/validation/alert-action-link";
import {AlertType} from "@component/validation/alert-type";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {SharedWitnessHostClusterData} from "@generated/shared-witness-host-cluster-data";
import {SharedWitnessHostService} from "@generated/shared-witness-host-service";
import {SharedWitnessLimits} from "@generated/shared-witness-limits";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {VsanStretchedClusterService} from "@generated/vsan-stretched-cluster-service";
import {ReassignWitnessContext} from "@navigation/context/reassign-witness-context";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {NavigationService, PlatformExtension} from "@service/navigation/navigation.service";
import {ScreenReaderAnnouncer, SrMessageType} from "@service/screen-reader-announcer.service";
import {TaskMonitorService} from "@service/task-monitor-service";
import {Icon} from "@util/icon";
import {Logger} from "@util/logger";
import {ModalBuilder} from "@util/modal-builder";
import {VsanUiUtils} from "@util/vsan-util";
import {SharedWitnessConstants} from "../common/shared-witness-constants";
import {SharedWitnessRoutePaths} from "../common/shared-witness-route-paths";

@Component({
   templateUrl: "shared-witness-clusters.html",
   styleUrls: ["shared-witness-clusters.scss"],
   selector: "vsan-shared-witness-clusters",
})
export class SharedWitnessClustersComponent implements OnInit {

   @LoadingIndicator()
   loading: boolean;

   @ErrorMessage()
   errorMessage: string;

   alertType: AlertType;
   addClustersActionTitle: string = "";
   isAddClustersActionDisabled: boolean = true;
   hasEditPermissions: boolean = false;
   clustersData: SharedWitnessHostClusterData[];
   selectedClusters: SharedWitnessHostClusterData[] = [];
   witnessLimits: SharedWitnessLimits;
   componentsWarningThreshold: number;
   showComponentsLimitAlert: boolean;
   componentsLimitAlertType: AlertType;
   componentsLimitAlertMessage: string;
   unavailableViewSpec: UnavailableViewSpec;

   _objectsCountFilterValue: number[] = [null, null];

   get objectsCountFilterValue(): number[] {
      return this._objectsCountFilterValue;
   }

   set objectsCountFilterValue(value: number[]) {
      this._objectsCountFilterValue = value;
      // Trigger model change for the label as it is inside content projection
      this.thresholdFilterActionLink.label = this.getComponentsThresholdFilterLabel();
   }

   clusterObjectsCountFilter: ClrDatagridNumericFilterInterface<SharedWitnessHostClusterData> =
         new class implements ClrDatagridNumericFilterInterface<SharedWitnessHostClusterData> {
            accepts = (item: SharedWitnessHostClusterData, low: number, high: number): boolean => {
               if (low !== null && item.componentsNumber < low) {
                  return false;
               }
               if (high !== null && item.componentsNumber > high) {
                  return false;
               }
               return true;
            };
         }();

   clusterNameFilter: ClrDatagridStringFilterInterface<SharedWitnessHostClusterData> =
         new class implements ClrDatagridStringFilterInterface<SharedWitnessHostClusterData> {
            accepts = (item: SharedWitnessHostClusterData, search: string): boolean => {
               return item.cluster.name !== null && item.cluster.name.toLowerCase().indexOf(search) > -1;
            };
         }();

   thresholdFilterActionLink: AlertActionLink = {
      label: this.getComponentsThresholdFilterLabel(),
      action: () => this.filterClustersByComponentsThreshold(),
   };

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   constructor(private sharedWitnessService: SharedWitnessHostService,
         private stretchedClusterService: VsanStretchedClusterService,
         private permissionService: PermissionService,
         private navigationService: NavigationService,
         private taskSvc: TaskMonitorService,
         private vsanCapabilityProvider: VsanCapabilityProvider) {
   }

   ngOnInit(): void {
      this.fetch();
   }

   @RefreshHandler
   @Loader({
      srMessage: "vsan.host.monitor.sharedWitnessClustersList.title",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   async fetch(): Promise<void> {
      this.resetData();
      const isSharedWitnessSupported: boolean = await this.vsanCapabilityProvider.getIsSharedWitnessSupported(
            ManagedObject.contextObject);
      if (!isSharedWitnessSupported) {
         this.unavailableViewSpec = UnavailableViewSpec.SHARED_WITNESS_NOT_SUPPORTED;
         return;
      }
      [this.clustersData, this.witnessLimits, this.hasEditPermissions] = await Promise.all([
         this.sharedWitnessService.getWitnessHostData(ManagedObject.contextObject),
         this.sharedWitnessService.getSharedWitnessLimits(ManagedObject.contextObject),
         this.permissionService.hasPermissions(ManagedObject.contextObject, [Privileges.EDIT_CLUSTER()]),
      ]);
      this.updateSelection();
      this.componentsWarningThreshold = this.getComponentsWarningThreshold();
      this.ensureComponentsLimitAlert();
      this.updateActionsAvailability();
      if (!this.witnessLimits) {
         this.alertType = AlertType.WARNING;
         throw new Error(VsanUiUtils.getString("vsan.sharedWitness.limits.query.error"));
      }
   }

   private resetData(): void {
      this.errorMessage = null;
      this.alertType = AlertType.DANGER;
      this.showComponentsLimitAlert = false;
      this.componentsLimitAlertType = AlertType.WARNING;
      this.componentsLimitAlertMessage = null;
   }

   private updateActionsAvailability(): void {
      this.isAddClustersActionDisabled = false;
      this.addClustersActionTitle = VsanUiUtils.getString(
            "vsan.host.monitor.sharedWitnessClustersList.addClusters.title");
      if (!this.clustersData || !this.witnessLimits) {
         // The data is still not initialized, should not happen, just sanity check.
         return;
      }
      if (VsanUiUtils.isNumericValue(this.witnessLimits.maxWitnessClusters) &&
            (this.clustersData.length >= this.witnessLimits.maxWitnessClusters)) {
         // The host already supports its max number of clusters
         this.isAddClustersActionDisabled = true;
         this.addClustersActionTitle = VsanUiUtils.getString(
               "vsan.host.monitor.sharedWitnessClustersList.addClusters.disabled.tooManyClusters",
               this.witnessLimits.maxWitnessClusters);
      } else if (VsanUiUtils.isNumericValue(this.witnessLimits.maxComponentsPerCluster) &&
            (this.witnessLimits.maxComponentsPerCluster <= this.getCurrentMaxComponentsCount())) {
         // If we add even 1 cluster to this host, the components limit will be exceeded for some clusters.
         this.isAddClustersActionDisabled = true;
         this.addClustersActionTitle = VsanUiUtils.getString(
               "vsan.host.monitor.sharedWitnessClustersList.addClusters.disabled.notEnoughComponents");
      }
   }

   private updateSelection(): void {
      // On reassign completed when data is fetched again clustersData may be empty
      if (!VsanUiUtils.isEmpty(this.selectedClusters) && !VsanUiUtils.isEmpty(this.clustersData)) {
         this.selectedClusters = this.clustersData.filter(
               c => this.selectedClusters.some(s => s.cluster.moRef.value === c.cluster.moRef.value));
      } else {
         this.selectedClusters = [];
      }
   }

   private getCurrentMaxComponentsCount(): number {
      return this.clustersData.reduce((max: number, clusterData: SharedWitnessHostClusterData) =>
            max = clusterData.componentsNumber > max
                  ? clusterData.componentsNumber
                  : max, 0);
   }

   private getComponentsWarningThreshold(): number {
      if (!this.witnessLimits || !this.witnessLimits.maxComponentsPerCluster) {
         return 0;
      }
      return this.witnessLimits.maxComponentsPerCluster * SharedWitnessConstants.CLUSTER_COMPONENTS_THRESHOLD_PERC;
   }

   private ensureComponentsLimitAlert(): void {
      this.showComponentsLimitAlert = this.isAnyClusterReachingComponentsLimit();
      if (this.showComponentsLimitAlert) {
         const hasAnyClusterReachedComponentsLimit: boolean =
               this.clustersData.some(c => this.hasReachedComponentsLimit(c));
         this.componentsLimitAlertType = hasAnyClusterReachedComponentsLimit
               ? AlertType.DANGER
               : AlertType.WARNING;
         this.componentsLimitAlertMessage = VsanUiUtils.getString(hasAnyClusterReachedComponentsLimit
               ? "vsan.host.monitor.sharedWitnessClustersList.clusters.warning.reachedLimit"
               : "vsan.host.monitor.sharedWitnessClustersList.clusters.warning.reachingLimit");
      }
   }

   private isAnyClusterReachingComponentsLimit(): boolean {
      return !VsanUiUtils.isEmpty(this.clustersData) &&
            this.clustersData.some(c => this.isReachingComponentsLimit(c));
   }

   getReassignWitnessActionTitle(): string {
      return VsanUiUtils.getString(this.selectedClusters.length
            ? "vsan.host.monitor.sharedWitnessClustersList.reassignWitness.title"
            : "vsan.host.monitor.sharedWitnessClustersList.reassignWitness.disabled.noSelection");
   }

   isReachingComponentsLimit(cluster: SharedWitnessHostClusterData): boolean {
      return VsanUiUtils.isNumericValue(this.componentsWarningThreshold) &&
            cluster.componentsNumber >= this.componentsWarningThreshold;
   }

   getComponentsLimitLabel(cluster: SharedWitnessHostClusterData): string {
      const localizationKey: string = this.hasReachedComponentsLimit(cluster)
            ? "vsan.host.monitor.sharedWitnessClustersList.cluster.warning.reachedLimit"
            : "vsan.host.monitor.sharedWitnessClustersList.cluster.warning.reachingLimit";
      return VsanUiUtils.getString(localizationKey, this.witnessLimits.maxComponentsPerCluster);
   }

   getComponentsLimitShape(cluster: SharedWitnessHostClusterData): string {
      return this.hasReachedComponentsLimit(cluster)
            ? Icon.CLR_ERROR_STANDARD_ICON_SHAPE
            : Icon.CLR_WARN_STANDARD_ICON_SHAPE;
   }

   getComponentsLimitClass(cluster: SharedWitnessHostClusterData): string {
      return Icon.clrIconShapeClassMap[this.getComponentsLimitShape(cluster)];
   }

   private hasReachedComponentsLimit(cluster: SharedWitnessHostClusterData): boolean {
      return cluster.componentsNumber >= this.witnessLimits.maxComponentsPerCluster;
   }

   filterClustersByComponentsThreshold(): void {
      const minValue: number = this.isComponentsThresholdFilterApplied()
            ? null
            : this.componentsWarningThreshold;
      const maxValue: number = this.objectsCountFilterValue[1];
      this.objectsCountFilterValue = [minValue, maxValue];
   }

   private getComponentsThresholdFilterLabel(): string {
      return VsanUiUtils.getString(this.isComponentsThresholdFilterApplied()
            ? "vsan.host.monitor.sharedWitnessClustersList.clusters.warning.clearFilter"
            : "vsan.host.monitor.sharedWitnessClustersList.clusters.warning.applyFilter");
   }

   private isComponentsThresholdFilterApplied(): boolean {
      return this.objectsCountFilterValue[0] === this.componentsWarningThreshold;
   }

   navigate(clusterRef: ManagedObject): void {
      this.navigationService.navigateTo(PlatformExtension.CLUSTER_FAULT_DOMAIN, clusterRef);
   }

   async addClusters(): Promise<void> {
      const eventTarget: EventTarget = event.target;
      let hasDiskGroups: boolean = false;
      try {
         hasDiskGroups = await this.stretchedClusterService.hasDiskGroups(ManagedObject.contextObject);
      } catch (error) {
         // Don't cancel add clusters dialog opening if disk groups is not fetched.
         Logger.error(error);
      }
      let appId: string = SharedWitnessRoutePaths.ADD_CLUSTERS_WIZARD_FULL;
      let modalSize: number[] = ModalBuilder.WIZARD_XL;
      if (hasDiskGroups) {
         appId = SharedWitnessRoutePaths.SELECT_CLUSTERS_DIALOG_FULL;
         modalSize = [700, 700];
      }
      new ModalBuilder(appId)
            .setDimensions(modalSize)
            .setEventTarget(eventTarget)
            .open()
            .then(this.refreshOnTaskCompleted);
   }

   reassignWitness(): void {
      const context: ReassignWitnessContext = {
         clustersData: this.selectedClusters.map(c => c.cluster),
         currentWitnessHost: ManagedObject.contextObject,
      };
      new ModalBuilder(SharedWitnessRoutePaths.REASSIGN_WITNESS_WIZARD_FULL)
            .setDimensions(ModalBuilder.WIZARD_XL)
            .open(context)
            .then(this.refreshOnTaskCompleted);
   }

   private refreshOnTaskCompleted = (task: ManagedObject) => {
      if (!task) {
         return;
      }
      this.taskSvc.getAwaitResult(task).then(() => this.fetch());
   };

   // Returns "a (out of max b)" label. If b is null, it is replaced by "--".
   getAssignedClustersValueLabel(): string {
      const naLabel: string = VsanUiUtils.getString("vsan.na.label");
      const assignedClustersCount: number = !VsanUiUtils.isEmpty(this.clustersData)
            ? this.clustersData.length
            : 0;
      const maxWitnessClusters: number | string =
            (this.witnessLimits && VsanUiUtils.isNumericValue(this.witnessLimits.maxWitnessClusters))
                  ? this.witnessLimits.maxWitnessClusters
                  : naLabel;
      return VsanUiUtils.getString("vsan.host.monitor.sharedWitnessClustersList.clusters.limit.value",
            assignedClustersCount, maxWitnessClusters);
   }

   getComponentsLimitValueLabel(): string | number {
      return this.witnessLimits && VsanUiUtils.isNumericValue(this.witnessLimits.maxComponentsPerCluster)
            ? this.witnessLimits.maxComponentsPerCluster
            : VsanUiUtils.getString("vsan.na.label");
   }
}