import {Injectable} from "@angular/core";
import {AlertType} from "@component/validation/alert-type";
import {InventoryNode as InventoryNodeInfo} from "@generated/inventory-node";
import {SharedWitnessClusterValidationData} from "@generated/shared-witness-cluster-validation-data";
import {SharedWitnessValidationData} from "@generated/shared-witness-validation-data";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {WitnessHostValidationResult} from "@generated/witness-host-validation-result";
import {VsanUiUtils} from "@util/vsan-util";

/**
 * Provides validation for a selected witness host.
 */
@Injectable()
export class WitnessValidationService {
   handleSharedWitnessHostValidationResult(hostValidationData: WitnessHostValidationResult): WitnessHostValidation {
      return this.handleHostValidationResult(hostValidationData, null, true);
   }

   handleSingleClusterWitnessValidationResult(hostValidationData: WitnessHostValidationResult,
         vsanConfig: VsanConfigSpec): WitnessHostValidation {
      return this.handleHostValidationResult(hostValidationData, vsanConfig, false);
   }

   /**
    * Returns a single host level validation result.
    * @param hostValidationData - The java side validation result
    * @param vsanConfig - Contains the cluster's config. Used in single cluster scenarios like configure vSAN or HCI
    * @param aggregateClusterErrors - Whether to report the cluster level errors as a host level error or return an
    * aggregated error message.
    * This is needed in shared witness scenarios, in order to show a general error message at the top of the page.
    * We already show the per cluster error in the validation grid, so there is no information hiding.
    * In the single cluster scenarios we show the cluster error from backend as it is more informative.
    */
   private handleHostValidationResult(hostValidationData: WitnessHostValidationResult, vsanConfig: VsanConfigSpec,
         aggregateClusterErrors: boolean): WitnessHostValidation {

      // First check the basics: is a connected host, is not in mm, has configured nic, etc.
      let validationMessages: WitnessHostValidation = this.validateWitnessCandidate(hostValidationData);
      if (validationMessages) {
         return validationMessages;
      }

      // If all looks good, check if the shared witness host validation has passed
      validationMessages = this.validateSharedWitnessHost(hostValidationData, vsanConfig, aggregateClusterErrors);
      if (validationMessages) {
         return validationMessages;
      }

      // Validate some conditions, to be reported as warnings
      validationMessages = this.validateWarningConditions(hostValidationData.isEncrypted);
      if (validationMessages) {
         return validationMessages;
      }
      // Validate some minor conditions reported as infos
      if (hostValidationData.vLCMToBeDisabled) {
         return new WitnessHostValidation(AlertType.INFO,
               [VsanUiUtils.getString("vsan.sharedWitness.assign.vLCM.warning")]);
      }
      // All is good, return validation success
      return new WitnessHostValidation(
            AlertType.SUCCESS, [
               VsanUiUtils.getString(
                     "vsan.faultDomains.stretchedCluster.wizard.configure.page.witnessHost.validation.compatibleHostState"),
            ]);
   }

   // Checks the basic host properties, such as connection state, powered state, etc..
   private validateWitnessCandidate(hostValidationResult: WitnessHostValidationResult): WitnessHostValidation {
      if (hostValidationResult.isHostInTheSameCluster) {
         return new WitnessHostValidation(
               AlertType.DANGER, [
                  VsanUiUtils.getString(
                        "vsan.faultDomains.stretchedCluster.wizard.configure.page.witnessHost.validation.internalHostSelectedError"),
               ]);
      } else if (hostValidationResult.isHostInVsanEnabledCluster) {
         return new WitnessHostValidation(
               AlertType.DANGER, [
                  VsanUiUtils.getString(
                        "vsan.faultDomains.stretchedCluster.wizard.configure.page.witnessHost.validation.hostInVsanEnabledCluster"),
               ]);
      } else if (hostValidationResult.isHostDisconnected) {
         return new WitnessHostValidation(
               AlertType.DANGER, [
                  VsanUiUtils.getString(
                        "vsan.faultDomains.stretchedCluster.wizard.configure.page.witnessHost.validation.disconnectedHostState"),
               ]);
      } else if (!hostValidationResult.isPoweredOn) {
         return new WitnessHostValidation(
               AlertType.DANGER, [
                  VsanUiUtils.getString(
                        "vsan.faultDomains.stretchedCluster.wizard.configure.page.witnessHost.validation.notPoweredOnHostState"),
               ]);
      } else if (hostValidationResult.isHostInMaintenanceMode) {
         return new WitnessHostValidation(
               AlertType.DANGER, [
                  VsanUiUtils.getString(
                        "vsan.faultDomains.stretchedCluster.wizard.configure.page.witnessHost.validation.maintenanceModeHostState"),
               ]);
      } else if (!hostValidationResult.hasVsanEnabledNic) {
         return new WitnessHostValidation(
               AlertType.DANGER, [
                  VsanUiUtils.getString(
                        "vsan.faultDomains.stretchedCluster.wizard.configure.page.witnessHost.validation.noVsanNicEnabledOnHost"),
               ]);
      } else if (!hostValidationResult.isVsanMaxCompatible) {
         return new WitnessHostValidation(
               AlertType.DANGER, [
                  VsanUiUtils.getString(
                        "vsan.faultDomains.stretchedCluster.wizard.configure.page.witnessHost.validation.vsanMax.hostNotSupported"),
               ]);
      }
      return null;
   }

   private validateSharedWitnessHost(hostValidationData: WitnessHostValidationResult,
         vsanConfig: VsanConfigSpec, aggregateClusterErrors: boolean): WitnessHostValidation {
      if (hostValidationData.isWitnessHost && !hostValidationData.isStretchedClusterSupported) {
         return new WitnessHostValidation(AlertType.DANGER, [
            VsanUiUtils.getString(
                  "vsan.faultDomains.stretchedCluster.wizard.configure.page.witnessHost.validation.featureNotSupportedOnHost"),
         ]);
      }

      if (hostValidationData.witnessValidationResult) {
         // Check if any API validation errors are returned for the witness host
         if (!VsanUiUtils.isEmpty(hostValidationData.witnessValidationResult.witnessHostValidationErrors)) {
            return new WitnessHostValidation(
                  AlertType.DANGER, hostValidationData.witnessValidationResult.witnessHostValidationErrors);
         }

         // Check if any API validation errors are returned for the clusters. Aggregate them to a single host error
         if (hostValidationData.witnessValidationResult.clustersValidation) {
            const clusterValidationResults: SharedWitnessClusterValidationData[] =
                  Object.values(hostValidationData.witnessValidationResult.clustersValidation);
            const clusterValidationError: SharedWitnessClusterValidationData = clusterValidationResults
                  .find(result => !result.compatible);
            if (clusterValidationError) {
               if (aggregateClusterErrors) {
                  return new WitnessHostValidation(AlertType.DANGER, [
                     VsanUiUtils.getString(
                           "vsan.host.sharedWitness.reassign.wizard.selectClusters.validation.incompatibleClusters"),
                  ]);
               } else {
                  return new WitnessHostValidation(AlertType.DANGER, [clusterValidationError.validationMessage]);
               }
            }

         }
      }

      // RDMA is not supported when shared witness host is used
      if (vsanConfig?.enableRdma && hostValidationData.isWitnessHost) {
         return new WitnessHostValidation(AlertType.DANGER,
               [VsanUiUtils.getString("vsan.sharedWitness.assign.rdma.error")]);
      }

      // Finally check that if a shared witness host (which don't support DIT) is used when the DiT encryption is
      // enabled
      if (vsanConfig?.enableDataInTransitEncryption && hostValidationData.isWitnessHost
            && !hostValidationData.isDitSharedWitnessInteroperabilitySupported) {
         /**
          * When Data-In-Transit Encryption is enabled, a shared witness host cannot be used.
          */
         return new WitnessHostValidation(AlertType.DANGER,
               [VsanUiUtils.getString("vsan.sharedWitness.assign.dit.error")]);
      }
      return null;
   }

   private validateWarningConditions(isEncrypted: boolean): WitnessHostValidation {
      if (isEncrypted) {
         return new WitnessHostValidation(AlertType.WARNING, [
            VsanUiUtils.getString(
                  "vsan.faultDomains.stretchedCluster.wizard.configure.page.witnessHost.validation.hostIsEncrypted"),
         ]);
      }
      return null;
   }

   handleHostValidationError(error: string): WitnessHostValidation {
      return new WitnessHostValidation(AlertType.DANGER, [error]);
   }

   updateClustersValidation(witnessValidationResult: SharedWitnessValidationData,
         clustersValidationData: Map<string, SharedWitnessClusterValidationData>,
         twoNodeClusters: InventoryNodeInfo[]): Map<string, SharedWitnessClusterValidationData> {
      if (!witnessValidationResult) {
         return clustersValidationData;
      }
      const validationData: any = witnessValidationResult.clustersValidation;
      if (!clustersValidationData) {
         // This is the first time we are doing the validation, just assign the result as is
         clustersValidationData = validationData;
         // If witness host validation is not passed cluster validation is null
      } else if (validationData) {
         // We need to only update the values, in case user deselected some of the clusters,
         // we are not getting them as part of the validation result, but still want to show the original errors
         for (const clusterData of twoNodeClusters) {
            const updatedValidationResult: any = validationData[clusterData.moRef.value];
            if (updatedValidationResult) {
               clustersValidationData[clusterData.moRef.value] = updatedValidationResult;
            }
         }
      }
      return clustersValidationData;
   }
}

export class WitnessHostValidation {
   constructor(
         public validationAlertType: AlertType,
         public validationMessages: string[],
   ) {
   }
}