import {AfterContentInit, ContentChildren, Directive, ElementRef} from "@angular/core";
import {ClrButton} from "@clr/angular";
import {ClrButtonBaseDirective, DirectiveHolder} from "@directive/clr-button-group/clr-button-base.directive";

/**
 * Applies on every clr-button-group and help to link the clr-button with directive applieds on it,  which extends
 * {@link ClrButtonBaseDirective}, to the actual rendered html button.
 */
@Directive({
   selector: "clr-button-group",
})
export class ClrButtonGroupCommonDirective implements AfterContentInit {

   @ContentChildren(ClrButton, {descendants: true})
   clrButtons: ClrButton[];

   constructor(private elRef: ElementRef) {}

   ngAfterContentInit(): void {
      setTimeout(() => {
         const allButtons: NodeListOf<HTMLButtonElement> = this.elRef.nativeElement.querySelectorAll("button");
         // match button with clrButton and notify directives
         this.clrButtons.filter(clrButton => !!(clrButton as DirectiveHolder).directives)
               .forEach((clrButton, index) => {
                  const buttonFound: HTMLButtonElement = this.findButtonByName(clrButton.name, allButtons);
                  if (buttonFound) {
                     (clrButton as DirectiveHolder).directives.forEach(directive => {
                        directive.resolveButton(clrButton, buttonFound, index);
                     });
                  }
               });
      });
   }

   private findButtonByName(name: string, buttons: NodeListOf<HTMLButtonElement>): HTMLButtonElement {
      for (let i: number = 0; i < buttons.length; i++) {
         if (buttons.item(i).name === name) {
            return buttons.item(i);
         }
      }
      return null;
   }

}